.Init-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('./fondo.jpg');
    background-position: top;
    background-size: cover;
}

.Init-logo-container {
    position:absolute;
    top:10%;
    position:absolute;
    width: 100%;
}
.Init-logo {
    max-width:80%;
    margin:0 auto;
    display:block;
}

.Init-spinner {
    width: 12px;
    display: block;
    margin: 0 auto;
}




